/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}


/* My css */

html, body, #root, #root > div, .screen {
  height: 100%;
}

body {
  text-align: center;
  font-size: 1.3em;
}

h1 {
  font-size: 2em;
  padding: 10px;
}

.screen {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

select {
  border: 0;
  background:goldenrod;
  color: white;
  width: 80%;
  margin: 5px auto;
  padding: 3px;
}

button {
  border: 1px solid gray;
}

.player-title {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.player-title button{
  -ms-flex-positive: 1;
      flex-grow: 1;
  margin: 5px;
  border: 0;
  background: goldenrod;
  color: white;
}
.player-title h1{
  -ms-flex-positive: 5;
      flex-grow: 5;
}


.revealer {
  -ms-flex-positive: 5;
      flex-grow: 5;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column; 
}
.revealer button {
  border: 2px dashed red;
  background: none;
  color: red;
  padding: 20px 5px;
  margin: 50px;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.revealer pre, .revealer p {
  border: 2px dashed gray;
  padding: 20px 10px;
  margin: 50px;
  -ms-flex-positive: 1;
      flex-grow: 1;
  vertical-align: middle;
}


